#modal { position: absolute; width: 100%; height: 100vh; left: 0px; top: 0px; } 
#modal .modal-section { border-radius: 5px; padding: 16px 28px 23px; background: rgb(255, 255, 255); min-width: 400px; position: fixed; top: 50%; left: 50%; transform: translate(-50%, -50%); z-index: 9000; } 
#modal .modal-title { font-size: 25px; font-weight: bold; padding-bottom: 10px; border-bottom: 1px solid #ccc; margin-bottom: 10px; } 
#modal .modal-text { margin: 20px 0; font-size: 15px; } 
#modal .close-modal { float: right; } 