@charset "utf-8";

* { box-sizing: border-box; -moz-box-sizing: border-box; }
table { border-collapse: collapse; border-spacing: 0; table-layout: fixed; width: 100%; }
h1, h2, h3, h4, h5, dl, dt, dd, ul, li, ol, th, td, p, blockquote, form, fieldset, legend, div,body { -webkit-print-color-adjust:exact; }
body { margin: 0; padding: 0; background-color: #FAFAFA; font-size: 11pt;font-family: 'Noto Sans KR', sans-serif; }
p { margin:0; padding:0; }
i { font-style: normal; }
.page { width: 21cm; min-height: 29.7cm; padding: 1cm; margin: 1cm auto; border: 1px #D3D3D3 solid; border-radius: 5px; background: white; box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); }
.subpage { padding: 30px;line-height:1.5; height: 276mm; outline: 1cm #ffffff solid; }
.head_text { margin-bottom: 5px; }
.head_text .date { font-weight: bold; font-size: 13px; margin-right: 20px; }
.page_wrapper { outline: 3px solid #000; height:980px; overflow:hidden;}
.page_wrapper .info_table { outline: 1px solid #000; }
.page_wrapper .info_table .head_title { border-bottom: 2px solid #000; font-size: 20px; }
table th, table td {border-right: 1px solid #000;border-bottom: 1px solid #000; }
.page_wrapper .info_table th { font-size: 14px; }
.page_wrapper .info_table td { font-size: 13px; text-align: center; padding: 5px; }
.page_wrapper .info_table .case-option-wrap { text-align: left; padding-left: 15px; }
.page_wrapper .info_table .case-option { margin-right: 30px; }
.page_wrapper .detail { padding: 10px 5px; font-size: 13px; }
.page_wrapper .detail .detail_title { margin: 0 0 20px; padding-left:0;}
.page_wrapper .detail ul {list-style:none;}
.page_wrapper .detail ul li.detail_content{ list-style: "-  "; margin-left:20px;word-break: break-word;}
ul li {word-break:keep-all; }
.page_wrapper .detail .writer { text-align: center; margin-bottom:0px;}

@page {
    size: A4;
    margin: 0;
}
@media print {
    .page {
        margin: 0;
        border: initial;
        border-radius: initial;
        width: initial;
        min-height: initial;
        box-shadow: initial;
        background: initial;
        page-break-inside: auto;
    }
}