
/* 상담내역히스토리 공통 스타일 */
.table.consult-history-table {border-bottom:1px solid #ccc;}
.table.consult-history-table:last-of-type td{border-top:1px solid #ccc; border-bottom:none;}
.table.consult-history-table .employee-code { color: #fff; display: inline-block; width: 33px; text-align: center; margin-right: 3px; } 
.table.consult-history-table .employee-receipt { background: #4472c4; } 
.table.consult-history-table .employee-sangdamsa { background:#767171; } 
.table.consult-history-table .employee-lawyer { background:#c55a11; width:45px; } 
.table.consult-history-table .employee-naesa { background:#999900; } 

/* 계약관리 - 법리검토보고서 테이블 */
.legal-report-info-table tr:first-of-type th, 
.legal-report-info-table tr:first-of-type td {border-top: none !important;}