.login-page { background: #f9f9f9; width: 100%; height: 100vh; display: flex; align-items: center; justify-content: center; } 
.login-page .login-form { border: 1px solid #d5d5d5; border-radius: 5px; padding: 16px 28px 23px; background: #fff; min-width: 400px; } 
.login-page .title { font-weight: bold; font-size: 30px; margin-bottom: 20px; } 
.login-page .row { border: 1px solid #ccc; border-radius: 5px; overflow: hidden; margin-bottom: 10px; height: 40px; position: relative; } 
.login-page .row .login-ico { position: absolute; left: 0; top: 50%; transform: translateY(-50%); color: #989898; background: #f4f4f4; height: 100%; width: 35px; border-right: 1px solid #ccc; padding: 10px; } 
.login-page .row .login-input { background: #fff; width: calc(100% - 35px); left: 35px; position: absolute; height: 100%; padding: 0 10px; } 
.login-page .checkbox-wrap label { vertical-align: 2px; margin-left: 5px; } 
.login-page .login-btn { float: right; } 