.total-sales-list .table {border-collapse: collapse;}
.total-sales-list .sales_summary {width:1000px; margin-left:auto;}
.total-sales-list .sales_summary .first-row th{border-bottom : none;}
.total-sales-list .sales_summary .first-row td{border-top:1px solid #ccc;}
.total-sales-list span.view-by-date {position:absolute; cursor: pointer; color: #777;}
.total-sales-list span.view-by-date.active {color:#111;}
.total-sales-list span.view-by-date:nth-child(1) {right:180px;}
.total-sales-list span.view-by-date:nth-child(2) {right:130px;}
.total-sales-list span.view-by-date:nth-child(3) {right:80px;}
.total-sales-list span.view-by-date:nth-child(4) {right:30px;}
.total-sales-list .sales_detail {float: left; width:calc(50% - 5px);}