.navi-wrap { width: 100%; border-bottom: 1px solid #d6d6d6; height: 56px; background:#fff; } 
.navi-wrap .page-container > article {float:left; width: 94px;  border-left:1px solid #d6d6d6; }
.navi-wrap .page-container > article:last-child {border-right:1px solid #d6d6d6; }
.navi-wrap .page-container > article.active {border-bottom: 3px solid #ff7f74}
.navi-wrap .page-container > article.active p,
.navi-wrap .page-container > article.active .navigation-ico {color:#383838 !important;}
.navi-list {display:block; text-align: center; width: 94%; padding-top: 10px; cursor: pointer; padding-bottom: 5px; margin:0 auto;} 
.navi-list:hover > p ,.navi-list:hover .navigation-ico {color:#888;}
.navi-list > p {color: #b2afaa;}
.navigation-ico {font-size:20px; color:#b2afaa}

.navi-list:hover .sub-menu-list {display:block;}
.navi-wrap .sub-menu-list { top: 55px; min-width:160px; display: none;} 
.navi-wrap .sub-menu-list li {line-height:18px; position: relative;}
.navi-wrap .sub-menu-list li a{ padding-right: 30px; padding-left:10px; font-size:12px;} 
.depts-3-lists {position: absolute; left: 158px; top: 0; width: 150px; display: none;}
.depts-3-lists:before {display: none !important;}
.depts-3-lists:after {display: none !important;}
.depts-3-wrap:hover .depts-3-lists{display:block; background:#f2f2f2;}