.scheduler-page .go-today { float: left; } 
.scheduler-page .month-btn { float: right; } 
.scheduler-page .month-btn > span { text-align: center; font-size: 18px; display: inline-flex; align-items: center; justify-content: center; margin-left: 5px; } 
.calendar-wrapper .date-txt { text-align: center; font-size:30px; } 

.date-head{float:left; width: 1382.97px;} 
.date-head .yoil {display: inline-block; width:214.84px; text-align: center; border: 1px solid #949494; font-size: 16px; border-right: none; padding: 4px 0; } 
.date-head .yoil.sunday {width:94px;}
.date-head .yoil:last-child { border-right: 1px solid #949494; } 

.calc-head{float: left;}
.calc-head .calc { display: inline-block; width:214.84px; text-align: center; border: 1px solid #949494; font-size: 16px; padding: 4px 0; border-left:none; background:aliceblue}

.date-body {float:left; width: 1382.97px;}
.scheduler-sect {border: 1px solid #949494; width: 214.84px; display: inline-block; padding: 5px 10px; min-height: 238.5px; border-right: none; border-top: none; float: left; }
.scheduler-sect.sunday {width: 94px;}
.scheduler-sect .date-num { text-align: right; margin-bottom: 10px; height:19.5px;} 
.scheduler-sect .scheduler-table { border-collapse: collapse; } 
.scheduler-sect .scheduler-table th { border: 1px solid #949494; padding: 2px 0; font-weight: normal;} 
.scheduler-sect .scheduler-table td { border: 1px solid #949494; } 

.scheduler-sect.sunday, .scheduler-sect.week:first-child {width:94px;}
.scheduler-sect.saturday,
.scheduler-sect.week:last-child {border-right:1px solid #949494;}
.scheduler-sect.today {outline: 2px solid #000}
.scheduler-sect.holiday {background:#ddd;}

.calc-body {float:left; width: 214.84px;}
.calc-body .scheduler-sect.week-calc{border-left:none; border-right:1px solid #949494; background:aliceblue;}