@charset "utf-8";
h1, h2, h3, h4, h5, dl, dt, dd, ul, li, ol, th, td, p, blockquote, form, fieldset, legend, div,body { -webkit-print-color-adjust:exact; } 
body { margin: 0; padding: 0; background-color: #FAFAFA; font: 12pt; font-family: 'Noto Sans KR', sans-serif; } 
* { box-sizing: border-box; -moz-box-sizing: border-box; } 

:global .page-component { width: 21cm; min-height: 29.7cm; padding: 1cm; margin: 1cm auto; border: 1px solid #d3d3d3; border-radius: 5px; background: #fff;} 
.subpage { padding: 30px; height: 276mm; outline: 1cm solid #fff; } 

@page { size: A4; margin: 0; } 
@media print {
    :global .page-component { margin: 0; border: initial; border-radius: initial; width: initial; min-height: initial; box-shadow: initial; background: initial; page-break-after: always; } 
}

.print_tit { font-size: 30px; text-align: center; margin: 20px 0; font-weight: bold; } 
.print_tit_small { font-size: 20px; text-align: center; margin: 30px 0; font-weight: bold; } 
.part { font-size:16px; } 
.part .part_tit { width:150px; display: inline-block; } 
.part .part_con { width: calc(100% - 150px); display: inline-block; vertical-align: top; } 
.noti_con { font-size: 15px; margin: 40px 0; } 
.report_article { font-size: 15px; } 
.report_article .article_title { margin:20px 0; } 
.report_article .korean_list li { list-style: hangul; margin-left: 22px; font-weight: bold; } 
.report_article .korean_list span { font-weight: normal; padding-left: 10px; word-break: keep-all; display: block; } 
.sign_wrap { text-align: right; margin-top: 40px; } 
.sign_wrap .sign_line { margin-bottom:20px; } 
.sign_wrap .sign_line:first-child { margin-bottom:80px; } 
.sign_wrap .today { font-size: 15px; margin-bottom: 20px; } 
.sign_wrap .sign_line .sign_tit { font-weight: bold; font-size: 15px; margin-right: 100px; } 