.case-update-modal { position: fixed; top: 50%; left: 50%; transform: translate(-50%,-50%); height: 136px; background: #eee; border: 2px solid #000; display: flex; align-items: center; justify-content: center; padding: 0 50px;} 
.case-update-modal .close-btns { font-size: 24px; cursor: pointer; line-height: 1; margin: 0; padding: 0; position: absolute; right: 10px; top: 10px; } 
.case-update-modal .update-box .update-sect { display: inline-block; margin-right: 8px; } 
.case-update-modal .update-box .update-sect label { display: block; padding-left: 3px; padding-bottom: 2px; color: #999; } 