@charset "utf-8";
h1, h2, h3, h4, h5, dl, dt, dd, ul, li, ol, th, td, p, blockquote, form, fieldset, legend, div,body { -webkit-print-color-adjust:exact; } 
body { margin: 0; padding: 0; background-color: #FAFAFA; font: 12pt; font-family: 'Noto Sans KR', sans-serif; } 
* { box-sizing: border-box; -moz-box-sizing: border-box; } 
.page { width: 21cm; min-height: 29.7cm; padding: 1cm; margin: 1cm auto; border: 1px #D3D3D3 solid; border-radius: 5px; background: white; box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); } 
.subpage { padding: 0; border: 3px #050505 double; height: 276mm; outline: 1cm #ffffff solid; /*background-image:url('/chart/page/user/img/img02.png'); */ } 

@page { size: A4; margin: 0; } 

@media print { 
    .page { margin: 0; border: initial; border-radius: initial; width: initial; min-height: initial; box-shadow: initial; background: initial; page-break-after: always; } 
 }
.title { text-align:center; margin:0 auto; font-size: 25px; text-align:center; margin-left:20px; margin: 10px 0; } 
.oper1 { border-bottom:2px solid; margin-left:20px; margin-right:10px; width:25%; float:left; font-size:15px; font-weight:bold; display:block; } 
.oper2 { border-bottom:2px solid; margin-right:10px; width:25%; float:left; font-size:15px; font-weight:bold; display:block; } 
.oper2_2 { border-bottom:2px solid; margin-right:10px; width:40%; float:left; font-size:15px; font-weight:bold; display:block; } 
.oper3 { margin-left:20px; width:25%; float:left; font-size:15px; margin-top: 10px; display:block; } 
.oper4 { margin-right:10px; width:25%; float:left; font-size:15px; margin-top: 10px; display:block; } 
.oper4_2 { margin-right:10px; width:40%; float:left; font-size:15px; margin-top: 10px; display:block; } 
.oper5 { border-bottom:2px solid; margin: 85px 20px 10px 20px; font-size:15px; font-weight:bold; display:block; } 
.oper6 { border-bottom:2px solid; margin:10px 20px 0px 20px; font-size:15px; font-weight:bold; display:block; padding:5px; line-height:20px; } 
table { width:100%; margin:0 auto; } 
table td { margin:5px; } 
.oper_list { margin-left:20px; margin-right:20px; } 
table.oper_list_table { border-collapse: collapse; border:1px solid; border-spacing:15px 0; } 
.oper_list_table th { width:25%; font-size:12px; font-weight:bold; padding:5px; border-collapse: collapse; border:1px solid } 
.oper_list_table td { width:25%; font-size:12px; padding:5px; border-collapse: collapse; border:1px solid } 
.oper_list_table tr { width:25%; margin-top:12px; border-collapse: collapse; } 
.oper_list_table td:nth-child(2n-1) { font-weight:bold } 
.table_oper3 { font-weight:lighter; font-size:12px; } 
.table_oper3 td { min-height:18px; } 

.oper_list_table2 th { padding:10px 0; min-width:100px; } 
.oper_list_table2 td { padding:5px; font-size:13px; min-width:15%; } 
.oper_list_table2 { border-collapse: separate; border-spacing: 15px 0; } 