.paging-wrap { text-align: center; margin: 30px 0; } 
.paging-wrap .paging-el { display: inline-block; border: 1px solid #ddd; border-radius: 15px; padding: 5px 14px; margin: 0 2px; background: #fff; } 
.paging-wrap .paging-el:hover {background-color: #f5f5f5;} 
.paging-wrap .paging-count { display: inline-block; margin: 0 10px; } 
.paging-wrap .paging-count li {display: inline-block;}
.paging-wrap .paging-count .paging-el.active {background-color: #f4f4f4; color:#999;} 