@charset "utf-8";

.past-consult-modal { position: absolute; right: 0; background: #fff; padding: 15px; top: 150px; border: 3px solid #111; z-index: 100; } 
.past-consult-modal .btn-wrap { text-align: right; } 
.past-consult-modal .close-modal { cursor: pointer; font-size: 30px; } 
.past-consult-modal .consult-date,
.past-consult-modal .consult-info { font-size: 24px; font-weight: bold; } 
.past-consult-modal .table tr th:nth-child(3),
.past-consult-modal .table tr td:nth-child(3) {width:45%;}